import React from "react";
import CustomModal from "../../components/Modal/customModal";
import ReusableTable from "../Table/Table";

const SuccessModal = ({ show, onHide, responseData }) => {
  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Upload Results"
      size="lg"
      footerButtons={[
        {
          label: "Close",
          variant: "primary",
          onClick: onHide,
        },
      ]}
    >
      <div className="success-modal-content">
        <h4>Upload Results ({responseData.length} items)</h4>
        <ReusableTable
          columns={[
            { accessorKey: "name", header: "Name" },
            { accessorKey: "revision", header: "Revision" },
            {
              accessorKey: "status",
              header: "Status",
              cell: ({ getValue }) => {
                const status = getValue();
                return status === "Success" ? (
                  <span>{status}</span>
                ) : (
                  <span>{status}</span>
                );
              },
            },
            { accessorKey: "message", header: "Message" },
          ]}
          data={responseData}
          widgetType="Mass_Upload_Response"
        />
      </div>
    </CustomModal>
  );
};

export default SuccessModal;
